import React, { useEffect } from "react";
import { Switch, Route, useLocation, Redirect } from "react-router-dom";
import { PrivateRoute, DicernaAdminRoute } from "./PrivateRoute/PrivateRoute";
import Header from "./Components/Header/Header";
import SideBar from "./Components/SideBar/SideBar";
import { LoginPage } from "./Components/LoginPage/LoginPage";
import Search from "./Components/Search/Search";
import ChangePassword from './Components/ChangePassword/ChangePassword';
import Footer from './Components/Footer/footer';
import PrivacyPolicy from "./Components/PrivacyPolicy/PrivacyPolicy";
import TermsConditions from "./Components/TermsConditions/TermsConditions";
import UsersList from "./Components/UsersList/UsersList";
import Dashboard from "./Components/Dashboard/Dashboard";
import CreateUserRequest from "./Components/CreateUserRequest/CreateUserRequest"
import CreateUser from "./Components/CreateUser/CreateUser";
import RevokeUser from "./Components/RevokeUser/RevokeUser";
import RevokeUserRequest from "./Components/RevokeUserRequest/RevokeUserRequest";
import PageNotFound from "./Components/PageNotFound/PageNotFound";
import "./App.css";


const App = (props) => {
  const location = useLocation();
  useEffect(() => {
    (document.getElementById("content") && document.getElementById("content").scrollTo(0, 0));
    window.scrollTo(0, 0)
  }, [location.pathname]);
  const currentUser = JSON.parse(localStorage.getItem("cfuCurrentUser"))
  return (
    <>
      <>
        {currentUser ?
          <>
            <Header user={props.user} />
            <div className="wrapper">
              <SideBar />
              <div id="content" className="content">
                <Switch>
                  <PrivateRoute exact path="/change-password" component={ChangePassword} />
                  <PrivateRoute exact path="/" component={currentUser.user.users_module === 0 ? Search : Dashboard} />
                  <PrivateRoute exact path="/search" component={Search} />
                  <DicernaAdminRoute exact path="/create-user-request" component={CreateUserRequest} />
                  <DicernaAdminRoute exact path="/create-user" component={CreateUser} />
                  <DicernaAdminRoute exact path="/users-list" component={UsersList} />
                  <DicernaAdminRoute exact path="/revoke-user" component={RevokeUser} />
                  <DicernaAdminRoute exact path="/revoke-user-request" component={RevokeUserRequest} />
                  <Route exact path="/privacy-policy" component={PrivacyPolicy} />
                  <Route exact path="/terms-and-conditions" component={TermsConditions} />
                  <Route component={PageNotFound} />
                </Switch>
              </div>
            </div>
          </> : <Redirect to="/login" />}
        <Route exact path="/login" component={LoginPage} />
        <Route exact path="/privacy-policy" component={!currentUser && PrivacyPolicy} />
        <Route exact path="/terms-and-conditions" component={!currentUser && TermsConditions} />
        <Footer />
      </>
    </>
  );
}

export { App };