import "./lineChart.css";
import ReactTooltip from "react-tooltip";

export const Line4 = (props) => {
  return (
    <>
      <div className="lineWrapper">
        <div className="basisDiv"></div>
        <div className="eotDiv" ></div>
        <div className="cfuDiv" data-tip="" data-for="line4EOT"></div>
        <div className="statusDiv4" data-tip="" data-for="line4RandDate"></div>
        <ReactTooltip
          id="line4EOT"
          getContent={() => {
            return <div>Randomization Date</div>;
          }}
        />
        <ReactTooltip
          id="line4RandDate"
          getContent={() => {
            return <div>EOT Date</div>;
          }}
        />
      </div>
    </>
  );
};

export default Line4;
