import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { authenticationService } from "../../services/API";
import Moment from 'react-moment';
import loaderImage from './../../Assets/images/loader.svg'
import { ToastContainer, toast } from 'react-toastify';
import "./create-user.css"
import 'react-toastify/dist/ReactToastify.css';

const CreateUser = () => {
    const [state, setState] = useState({
        loader: true,
        requests: null
    })
    const notify = () => {
        toast.success("User Request created successfully !!!", {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        })
    }
    let createsLengthStorage = null
    useEffect(() => {
        authenticationService.getCreate().then(res =>
            setState({
                loader: false,
                requests: res.requests
            })
        )
    }, [])
    if (!localStorage.getItem("createsLengthStorage")) {
        if (state.requests) {
            localStorage.setItem("createsLengthStorage", false)
        }
    }
    createsLengthStorage = JSON.parse(localStorage.getItem("createsLengthStorage"))
    useEffect(() => {
        if (state.requests)
            if (createsLengthStorage) {
                notify()
                localStorage.setItem("createsLengthStorage", false)
            }
    }, [state.loader])
    return <>
        < ToastContainer />
        <div className="createBtnDiv">
            <Link className="createReqLink" to="/create-user-request">
                <button
                    className="revokeAccesBtn">
                    Create Request
                </button>
            </Link>
        </div>
        {state.loader ? <div className="loaderDiv"><img src={loaderImage} className="img-fluid" alt="" /></div> : null}
        {(state.requests &&
            <div className="usersListWrapper">
                <table className='custom-table  table-responsive-md '>
                    <thead >
                        <tr className="headTr">
                            <th>
                                <h4 className="headerTrTh">
                                    #
                                </h4>
                            </th>
                            <th>
                                <h4 className="headerTrTh">
                                    Access Requester
                                </h4>
                            </th>
                            <th>
                                <h4 className="headerTrTh">
                                    Name
                                </h4>
                            </th>
                            <th>
                                <h4 className="headerTrTh">
                                    Email
                                </h4>
                            </th>
                            <th>
                                <h4 className="headerTrTh">
                                    Site Name
                                </h4>
                            </th>
                            <th>
                                <h4 className="headerTrTh">
                                    Access Privileges
                                </h4>
                            </th>
                            <th className='createdAt '>
                                <h4 className="headerTrTh">
                                    Created at
                                </h4>
                            </th>
                            <th className='statusRevoke '>
                                <h4 className="headerTrTh">
                                    Status
                                </h4>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {state.requests.map((i, v) => {
                            return <tr key={v} className='tbodyTr'>
                                <td className="firsTd">
                                    <h4>{state.requests.length - v}</h4>
                                </td>
                                <td>
                                    <h4>{i.user.name}</h4>
                                </td>
                                <td>
                                    <h4>{i.name}</h4>
                                </td>
                                <td>
                                    <h4>{i.email}</h4>
                                </td>
                                <td>
                                    <h4>{i.site === "*" ? "All Sites" : i.site}</h4>
                                </td>
                                <td >
                                    <h4>{i.access_level}</h4>
                                </td>
                                <td className='lastTd'>
                                    <h4><Moment format="YYYY-MM-DD HH:mm:ss">{i.created_at}</Moment></h4>
                                </td>
                                <td className={i.status === "open" ? "statusOpenClass" : null}>
                                    <h4>{i.status}</h4>
                                </td>
                            </tr>
                        }
                        )}
                    </tbody>
                </table>
            </div>
        )}
    </>
}

export default CreateUser;