import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { default as Select } from "react-select";
import { authenticationService } from "../../services/API";
import "./create-request.css";
import loaderImage from "./../../Assets/images/loader.svg"
import { Redirect } from "react-router";
import { FaInfoCircle } from "react-icons/fa";
import ReactTooltip from "react-tooltip";


const CreateUserRequest = () => {
    let [state, setState] = useState({
        name: null,
        email: null,
        site: "",
        access_level: null
    })
    let [infoBuble, setInfoBuble] = useState(false)
    let [readOnly, setReadOnly] = useState(false)

    let [loader, setLoader] = useState(false)
    const [succesNoty, setSuccesNoty] = useState(false)
    const [selectValid, setSelectValid] = useState(false)

    useEffect(() => {
        if (JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module == 3) {
            setState({
                ...state,
                access_level: "Site User"
            })
        }
    }, [])


    const options = [
        { value: "Site User", label: "Site User" },
        { value: "Dicerna User", label: "Dicerna User" },
    ];
    return (
        <>
            {succesNoty && (<Redirect
                to={{
                    pathname: "/create-user",
                    state: { succesNotify: true }
                }}
            />
            )
            }
            {loader ? <div className="loaderDiv"><img src={loaderImage} className="img-fluid" alt="" /></div> : null}
            <div className="backUsersListDiv">
                <Link to="/create-user" className="toUsersListLink">
                    Back
                </Link>
            </div>
            <form onSubmit={(e) => {
                e.preventDefault();
                if (state.site.trim() && state.name.trim() && state.email.trim() && state.access_level) {
                    setLoader(true)
                    authenticationService.createUser(state.name, state.email, state.site, state.access_level).then(res => {
                        localStorage.setItem("createsLengthStorage", true)

                        setLoader(false)
                        setSuccesNoty(true)
                    })
                }
                else if (!state.access_level) {
                    setSelectValid(true)
                }
            }
            }>
                <div className="createRequestWrapper">
                    <div className="createRequestHeader">
                        <h4>
                            Create User
                        </h4>
                    </div>
                    <div className="createRequestMain">
                        <div className="userNameInfoDiv">
                            <label>User Name</label>
                            <FaInfoCircle data-tip="" data-for="userName" /> <ReactTooltip
                                id="userName"
                                getContent={() => {
                                    return <div>First Name and Last Name</div>;
                                }}
                            />
                        </div>
                        <input pattern=".*\S+.*" required type="text" name="username" placeholder="User Name" className="form-control" onChange={(e) => {
                            setState({
                                ...state,
                                name: e.target.value
                            })
                        }} />
                        <label>Email</label>
                        <input type="text" name="email" pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$" required placeholder="Email" className="form-control" onChange={(e) => {
                            setState({
                                ...state,
                                email: e.target.value
                            })
                        }} />
                        <label>Access Level</label>
                        {JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module == 2 ? <Select
                            pattern=".*\S+.*"
                            required
                            options={options}
                            className={selectValid && "selectAcces"}
                            onChange={(e) => {
                                if (e.value === "Dicerna User" || e.value === "Dicerna Admin") {
                                    setState({
                                        ...state,
                                        access_level: e.value,
                                        site: "All Sites"
                                    })
                                    setReadOnly(true)
                                    setInfoBuble(false)
                                }
                                else if (e.value === "Site User") {
                                    setState({
                                        ...state,
                                        access_level: e.value,
                                        site: ""
                                    })
                                    setReadOnly(false)
                                    setInfoBuble(true)
                                }
                                setSelectValid(false)
                            }}
                        /> : <input required name="site" type="text" placeholder="Please enter Site Number" value={"Site User"} className="form-control"
                            readOnly={true} />}
                        {selectValid && <h5 className="accessLevelValid">Please Select Access Level</h5>}
                        <div className="siteInfo">
                            <label>Site</label>
                            {(infoBuble || JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module == 3) ? <> <FaInfoCircle data-tip="" data-for="siteInfo" /> <ReactTooltip
                                id="siteInfo"
                                getContent={() => {
                                    return <div>Site Number: XXX-XXX</div>;
                                }}
                            /> </> : null}
                        </div>

                        <input required name="site" type="text" placeholder="Please enter Site Number" value={state.site} className="form-control" onChange={(e) => {
                            setState({
                                ...state,
                                site: e.target.value
                            })
                        }} readOnly={readOnly ? true : false} />
                    </div>
                    <div className="createRequestFooter">
                        <button type="submit" className='createUserBtn' >Submit</button>
                    </div>
                </div >
            </form>
        </>)
}

export default CreateUserRequest;