import "./dashboard.css";
import DashboardTable from "./DashboardTable/DashboardTable";
import { useSelector } from "react-redux";

const Dashboard = () => {
  var today = new Date();
  var date =
    today.getFullYear() + "-" + (today.getMonth() + 1) + "-" + today.getDate();
  const state = useSelector((state) => state)

  return (
    <>
      <div className="dashboardWrapper">
        <div className="titleDate">
          <div className="title">
            <h3>Summary Dashboard-CFU</h3>
          </div>
          <div className="today">
            <h3>{date}</h3>
          </div>
        </div>
        <div className="cardsWrapper">
          <div className="subjectsCard">
            <div className="cardHeader allSubjectsHeader">All Subjects</div>
            <span className="textInSubjectCard">
              <h4>{state.dataIsReturned && state.patientData.filter(i => i.status.id).length}</h4>
            </span>
          </div>
          <div className="randomizationCard">
            <div className="cardHeader randomizedHeader"> Randomized</div>
            <span className="textInSubjectCard">
              <h4>{state.dataIsReturned && state.patientData.filter(i => i.status.id > 1).length}</h4>
            </span>
          </div>
          <div className="inTEO">
            <div className="cardHeader EOTSubjectsHeader">In Treatment Window</div>
            <span className="textInSubjectCard">
              <h4>{state.dataIsReturned && state.patientData.filter(i => i.status.id === 2).length}</h4>
            </span>
          </div>
          <div className="notCFU">
            <div className="cardHeader CFUSubjectsHeader"> Reached EOT, Did Not Meet CFU</div>
            <span className="textInSubjectCard">
              <h4>{state.dataIsReturned && state.patientData.filter(i => i.status.id === 3).length}</h4>
            </span>
          </div>
          <div className="inCFU">
            <div className="cardHeader CFUSubjectsHeader">Met CFU Criteria</div>
            <span className="textInSubjectCard">
              <h4>{state.dataIsReturned && state.patientData.filter(i => i.status.id === 4).length}</h4>
            </span>
          </div>
        </div>
        <div className="dashboardTable">
          <DashboardTable />
          <div className="prejectDateDiv">*Note: Projected Date</div>
        </div>
      </div>
    </>
  );
};
export default Dashboard;
