import { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { default as Select } from "react-select";
import "react-data-table-component-extensions/dist/index.css";
import Line from "../TimeMaps/lineChart";
import Line2 from "../TimeMaps/lineChart2";
import Line3 from "../TimeMaps/lineChart3";
import Line4 from "../TimeMaps/lineChart4";
import { authenticationService } from "./../../../services/API";
import loaderImage from "./../../../Assets/images/loader.svg";
import "./dashboard-table.css";
import { useDispatch, useSelector } from "react-redux";
import DataTableExtensions from 'react-data-table-component-extensions';
import 'react-data-table-component-extensions/dist/index.css';

const customStyles = {
  headCells: {
    style: {
      backgroundColor: "#c1dbef",
    },
  },
  cells: {
    style: {
      fontSize: '12px'
    },
  },
};

const DashboardTable = () => {
  const [data, setData] = useState([]);
  const state = useSelector((state) => state)
  const dispatch = useDispatch()
  const getPatientsList = authenticationService.getPatientsList;
  const statuses = {
    status1: "SUBJECT WAS NOT RANDOMIZED",
    status2: "SUBJECT DID NOT REACH EOT",
    status3: "SUBJECT COMPLETED THE STUDY",
    status4: "SUBJECT ENTERED CFU"
  }
  let sortData = []
  let dataFilt = [];

  useEffect(() => {
    getPatientsList().then((res) => {
      dispatch({
        type: "SET-DATA",
        payload: {
          value: res
        }
      })
    })
  }, [])

  if (state.dataIsReturned) {
    state.patientData.map((i) => {
      if (i.status.id === 1) {
        dataFilt.push({
          "PATIENT ID": i.subject_id,
          "RANDOMIZATION DATE": i.randomization_date,
          "EOT DATE": i.eot_date,
          "STATUS": i.status.text.toUpperCase(),
        });
      } else if (i.status.id === 2) {
        dataFilt.push({
          "PATIENT ID": i.subject_id,
          "RANDOMIZATION DATE": i.randomization_date,
          "EOT DATE": i.eot_date,
          "STATUS": i.status.text.toUpperCase(),
        });
      } else if (i.status.id === 3) {
        dataFilt.push({
          "PATIENT ID": i.subject_id,
          "RANDOMIZATION DATE": i.randomization_date,
          "EOT DATE": i.eot_date,
          "STATUS": i.status.text.toUpperCase(),
        });
      } else if (i.status.id === 4) {
        dataFilt.push({
          "PATIENT ID": i.subject_id,
          "RANDOMIZATION DATE": i.randomization_date,
          "EOT DATE": i.eot_date,
          "STATUS": i.status.text.toUpperCase(),
        });
      }
    });

  }

  useEffect(() => {
    setData(dataFilt)
  }, [state.dataIsReturned])


  const filterFunction = (value) => {
    if (value === "1") {
      state.patientData.map(i => {
        if (i.status.id === 2) {
          sortData.push({
            "PATIENT ID": i.subject_id,
            "RANDOMIZATION DATE": i.randomization_date,
            "EOT DATE": i.eot_date,
            "STATUS": i.status.text.toUpperCase(),
          })
          setData(sortData)
        }
        else if (i.status.id === 3) {
          sortData.push({
            "PATIENT ID": i.subject_id,
            "RANDOMIZATION DATE": i.randomization_date,
            "EOT DATE": i.eot_date,
            "STATUS": i.status.text.toUpperCase(),
          })
        }
        else if (i.status.id === 4) {
          sortData.push({
            "PATIENT ID": i.subject_id,
            "RANDOMIZATION DATE": i.randomization_date,
            "EOT DATE": i.eot_date,
            "STATUS": i.status.text.toUpperCase(),
          })
        }
      })
      setData(sortData)

    }
    if (value === "2") {
      state.patientData.map(i => {
        if (i.status.id === 4) {
          sortData.push({
            "PATIENT ID": i.subject_id,
            "RANDOMIZATION DATE": i.randomization_date,
            "EOT DATE": i.eot_date,
            "STATUS": i.status.text.toUpperCase(),
          })
          setData(sortData)
        }
      })

    }
    if (value === "0") {
      setData(dataFilt)
    }
  }
  const columns = [
    {
      name: "PATIENT ID",
      selector: "PATIENT ID",
      sortable: true,
    },
    {
      name: "STATUS",
      selector: "STATUS",
      sortable: true,
      rigth: true,
      cell: (i) => <div className={i["STATUS"] === statuses.status1 ? "normalText randomizedDiv" :
        (i["STATUS"] === statuses.status2 ? "normalText status2" :
          (i["STATUS"] === statuses.status3 ? "normalText status3" : "normalText status4")
        )}>{i["STATUS"]}</div>,
    },
    {
      name: "RANDOMIZATION DATE",
      selector: "RANDOMIZATION DATE",
      sortable: true,
      right: true,
    },
    {
      name: "EOT DATE",
      selector: "EOT DATE",
      sortable: true,
      right: true,
      cell: (i) => i["STATUS"] === statuses.status2 ? <div className="redEOTDate"> {i["EOT DATE"]} </div> : <div className="darkBlack"> {i["EOT DATE"]} </div>
    },

    // {
    //   name: <Name />,
    //   selector: "A1AT",
    //   sortable: false,
    //   right: true,
    //   cell: (i) => {
    //     switch (i["STATUS"]) {
    //       case statuses.status1: return <div className="test1"> <div className="bNumber">N/A</div> <div className="eotNumber">N/A</div> <div>N/A</div> </div>;
    //       case statuses.status2: return <div className="test1"> <div className="bNumber">90</div> <div className="eotNumber">N/A</div> <div>N/A</div> </div>;
    //       case statuses.status3: return <div className="test2"> <div className="bNumber">84</div> <div className="eotNumber">102</div> <div>121.4%</div> </div>;
    //       case statuses.status4: return <div className="test2"> <div className="bNumber">135</div> <div className="eotNumber">88</div> <div>65.1%</div> </div>
    //     }
    //   }
    // },

    {
      name: "TIME/STATUS MAP",
      selector: "TIME/STATUS MAP",
      sortable: false,
      center: true,
      cell: (i) => i["STATUS"] === statuses.status1 ? <Line eotDate={i["EOT DATE"]} randDate={i["RANDOMIZATION DATE"]} /> :
        (i["STATUS"] === statuses.status2 ? <Line2 eotDate={i["EOT DATE"]} randDate={i["RANDOMIZATION DATE"]} /> :
          (i["STATUS"] === statuses.status3 ? <Line3 eotDate={i["EOT DATE"]} randDate={i["RANDOMIZATION DATE"]} /> :
            <Line4 eotDate={i["EOT DATE"]} randDate={i["RANDOMIZATION DATE"]} />)
        ),
    },
  ];

  const options = [
    { value: "0", label: "All" },
    { value: "1", label: "Randomized" },
    { value: "2", label: "Met CFU Criteria" },
  ];
  const tableData = {
    columns,
    data,
  };

  return (
    <>
      {state.loader ? (
        <div className="loaderDiv">
          <img src={loaderImage} className="img-fluid" alt="" />
        </div>
      ) : null}
      <div>
        <h1>Patients List</h1>
      </div>
      <div className="dropdwondiv">
        <div className="selectPopDiv">Select Population</div>
        <Select
          options={options}
          className="selectComp"
          onChange={(event) => filterFunction(event.value)}
        />
      </div>
      {state.dataIsReturned && (
        // <DataTableExtensions
        //   print={true}
        //   export={true}
        //   {...tableData}
        // >
          <DataTable
            noHeader
            title=""
            columns={columns}
            data={data}
            customStyles={customStyles}
            Clicked
          />
        //  </DataTableExtensions> 
      )}
    </>
  );
};

export default DashboardTable;