import "./lineChart.css";
import ReactTooltip from "react-tooltip";
export const Line3 = (props) => {
  return (
    <>
      <div className="lineWrapper">
        <div className="basisDiv"></div>
        <div className="eotDiv" data-tip="" data-for="line3RandDate"></div>
        <div className="cfuDiv" data-tip="" data-for="line3EOT"></div>
        <div className="statusDiv3" data-tip="" data-for="line3RandDate"></div>
        <ReactTooltip
          id="line3EOT"
          getContent={() => {
            return <div>Randomization Date</div>;
          }}
        />
        <ReactTooltip
          id="line3RandDate"
          getContent={() => {
            return <div>EOT Date</div>;
          }}
        />
      </div>
    </>
  );
};

export default Line3;
