import "./lineChart.css";
import ReactTooltip from "react-tooltip";
const Line2 = (props) => {
  var today = new Date();
  var date = today.getFullYear() + "-" + (today.getMonth() + 1) + "-" + today.getDate();
  // var date = "2021-12-21"
  const randDate = new Date(props.randDate);
  const eotDate = new Date(props.eotDate);
  const today1 = new Date(date);
  const diffTime = Math.abs(eotDate - randDate);
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  const diffTimeToday = Math.abs(randDate - today1);
  const diffDaysToday = Math.ceil(diffTimeToday / (1000 * 60 * 60 * 24));

  let param = 60 / diffDays
  let a = ((diffDaysToday) * param)
  let b = 0;

  b = (a + 17).toString() + "%"
  if (a > 63) {
    b = '77%'
  }
  // console.log(b)
  return (
    <>
      <div className="lineWrapper">
        <div className="basisDiv"></div>
        <div className="eotDiv" data-tip="" data-for="line2RandDate"></div>
        <div className="cfuDiv" data-tip="" data-for="line2EOT"></div>
        <div className="statusDiv2" style={{ left: b }}></div>
        <ReactTooltip
          id="line2EOT"
          getContent={() => {
            return <div>Randomization Date</div>;
          }}
        />
        <ReactTooltip
          id="line2RandDate"
          getContent={() => {
            return <div>EOT Date</div>;
          }}
        />
      </div>
    </>
  );
};
export default Line2;
