import "./lineChart.css";
import ReactTooltip from "react-tooltip";

export const Line = (props) => {
  return (
    <>
      <div className="lineWrapper">
        <div className="basisDiv"></div>
        <div className="eotDiv" data-tip="" data-for="line1EOT"></div>
        <div className="cfuDiv" data-tip="" data-for="line1RandDate"></div>
        <div className="statusDiv"></div>
        <ReactTooltip
          id="line1EOT"
          getContent={() => {
            return <div>EOT Date</div>;
          }}
        />
        <ReactTooltip
          id="line1RandDate"
          getContent={() => {
            return <div>Randomization Date</div>;
          }}
        />
      </div>
    </>
  );
};

export default Line;
