import React from "react";
import "./scoringResult.css";
import InputMask from "react-text-mask";

const ScoringResult = (props) => {

  const backBtnOnClickFn = () => {
    props.setPatientId('');
    props.setScoring(false);
  }
  return (
    <>
      <div className="scoringResultContainer">
        <div className="backBtnDiv">
          <button className='backBtn mb-3' onClick={backBtnOnClickFn}><div>Back</div></button>
        </div>
        <div className='scoringResultWrapper'>
          <div className='scoringResultHeader'>
            <h4 className='my-0'>Search results</h4>
          </div>
          <div className='scoringContent'>
            <div className='patientIdDiv'>
              <label>Patient ID</label>
              <InputMask
                className='form-control searchInput idInputDisable' value={props.siteId + props.patientId} disabled
                mask={[
                  /\d/,
                  /\d/,
                  /\d/,
                  "-",
                  /\d/,
                  /\d/,
                  /\d/,
                  "-",
                  /\d/,
                  /\d/,
                  /\d/,
                ]}
              />
            </div>
            <div className='patientStatusDiv' >
              <h4 className={
                props.patientStatusId === 1 ? "wasNotRand" : (props.patientStatusId === 2 ? "inEOT" : (props.patientStatusId === 3 ? "toFollowUp" : "cfuCriterieColor"))}>{props.patientStatus}</h4>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ScoringResult;