import "./SideBar.css";
import { NavLink } from 'react-router-dom';
import { FaChartBar, FaSearch, FaUserFriends, FaRegFilePdf, FaUserPlus, FaUserMinus } from "react-icons/fa";
import algorithmPDF from "./../../Assets/PDF/CFU_Description_v0.13_06-Oct-2021.pdf"

const SideBar = () => {
    return <>
        <nav className="main-menu">
            <ul className="sideBarItemsUl">
                {JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module > 0 &&
                    <li className="dashboard">
                        <NavLink exact activeClassName="active" to="/">
                            <FaChartBar className="sideBarIcons" />
                            <span className='nav-text'>
                                Dashboard
                            </span>
                        </NavLink>
                    </li>}
                {JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module > 0 ?
                    <li className="has-subnav search">
                        <NavLink exact activeClassName="active" to="/search">
                            <FaSearch className="sideBarIcons" />
                            <span className='nav-text'>
                                Search
                            </span>
                        </NavLink>
                    </li> : <li className="has-subnav search">
                        <NavLink exact activeClassName="active" to="/">
                            <FaSearch className="sideBarIcons" />
                            <span className='nav-text'>
                                Search
                            </span>
                        </NavLink>

                    </li>}
                {(JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module === 2
                    || JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module === 3) &&
                    <li className="has-subnav usersListEnable">
                        <NavLink activeClassName="active" to="/users-list">
                            <FaUserFriends className="sideBarIcons" />
                            <span className='nav-text'>
                                Users List
                            </span>
                        </NavLink>
                    </li>
                }
                {(JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module === 2
                    || JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module === 3) &&
                    <li className="has-subnav usersListEnable">
                        <NavLink activeClassName="active" to="/create-user">
                            <FaUserPlus className="sideBarIcons" />
                            <span className='nav-text'>
                                Create Request
                            </span>
                        </NavLink>
                    </li>
                }
                {(JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module === 2
                    || JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module === 3) &&
                    <li className="has-subnav usersListEnable">
                        <NavLink activeClassName="active" to="/revoke-user">
                            <FaUserMinus className="sideBarIcons" />
                            <span className='nav-text'>
                                Revoke Request
                            </span>
                        </NavLink>
                    </li>
                }
                <li className="has-subnav usersListEnable">
                    <NavLink activeClassName="active" target="_blank" to={algorithmPDF}>
                        <FaRegFilePdf className="sideBarIcons" />
                        <span className='nav-text'>
                            Algorithm Description
                        </span>
                    </NavLink>
                </li>
                {/* <li className="has-subnav usersListEnable">
                    <NavLink activeClassName="active"
                        to='/create-user-request'>
                        <FaRegFilePdf className="sideBarIcons" />
                        <span className='nav-text'>
                            create
                        </span>
                    </NavLink>
                </li> */}
                {/* <li className="has-subnav notifications">
                    <FaBell className="sideBarIcons" />
                    <span className='nav-text'>
                        Notifications
                    </span>
                </li> */}
            </ul>
        </nav>
    </>
}

export default SideBar;