import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { authenticationService } from "../../services/API";
import Moment from 'react-moment';
import loaderImage from './../../Assets/images/loader.svg'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import "./revoke-user.css"
// import { useSelector } from "react-redux";

const RevokeUser = (props) => {

    const notify = () => {
        toast.success("User Revoke Request created successfully !!!", {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        })
    }
    let revokesLengthStorage = null
    const [state, setState] = useState({
        loader: true,
        revokes: null,
        succesNotify: false
    })
    useEffect(() => {
        authenticationService.getRevoke().then(res =>
            setState({
                ...state,
                loader: false,
                revokes: res.revokes
            }),

        )
    }, [])

    if (!localStorage.getItem("revokesLength")) {
        if (state.revokes) {
            localStorage.setItem("revokesLength", false)
        }
    }
    revokesLengthStorage = JSON.parse(localStorage.getItem("revokesLength"))

    useEffect(() => {
        if (state.revokes)
            if (revokesLengthStorage) {
                notify()
                localStorage.setItem("revokesLength", false)
            }
    }, [state.loader])
    return <>
        < ToastContainer />
        <div className="createBtnDiv">
            <Link className="createReqLink" to="/revoke-user-request">
                <button
                    className="revokeAccesBtn"
                >
                    Revoke Access Request
                </button>
            </Link>
        </div>
        {state.loader ? <div className="loaderDiv"><img src={loaderImage} className="img-fluid" alt="" /></div> : null}
        {(state.revokes &&
            <div className="usersListWrapper">
                <table className='custom-table  table-responsive-md '>
                    <thead >
                        <tr className="headTr">
                            <th>
                                <h4 className="headerTrTh">
                                    #
                                </h4>
                            </th>
                            <th>
                                <h4 className="headerTrTh">
                                    Revoke Requester
                                </h4>
                            </th>
                            <th>
                                <h4 className="headerTrTh">
                                    User Name
                                </h4>
                            </th>
                            <th>
                                <h4 className="headerTrTh">
                                    User Email
                                </h4>
                            </th>
                            <th>
                                <h4 className="headerTrTh">
                                    Comment
                                </h4>
                            </th>
                            <th className='createdAt '>
                                <h4 className="headerTrTh">
                                    Created at
                                </h4>
                            </th>
                            <th className='statusRevoke '>
                                <h4 className="headerTrTh">
                                    Status
                                </h4>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {state.revokes.map((i, v) => {
                            return <tr key={v} className='tbodyTr'>
                                <td className="firsTd">
                                    <h4>{state.revokes.length - v}</h4>
                                </td>
                                <td>
                                    <h4>{i.creator ? i.creator.name : null}</h4>
                                </td>
                                <td>
                                    <h4>{i.user ? i.user.name : null}</h4>
                                </td>
                                <td>
                                    <h4>{i.user ? i.user.email : null}</h4>
                                </td>
                                <td>
                                    <h4>{i.comment}</h4>
                                </td>
                                <td className='lastTd'>
                                    <h4><Moment format="YYYY-MM-DD HH:mm:ss">{i.created_at}</Moment></h4>
                                </td>
                                <td className={i.status == "open" ? "statusOpenClass" : null} >
                                    <h4>{i.status}</h4>
                                </td>
                            </tr>
                        }
                        )}
                    </tbody>
                </table>
            </div>
        )}
    </>
}

export default RevokeUser;