import { authenticationService } from "./../../services/API";
import React, { useEffect, useState } from 'react'
import loaderImage from './../../Assets/images/loader.svg';
import Moment from 'react-moment';
// import DataTableExtensions from 'react-data-table-component-extensions';
import 'react-data-table-component-extensions/dist/index.css';
import './usersList.css'

const UsersList = () => {

    let tableData = {};
    const [data, setData] = useState([])
    const [state, setState] = useState({
        amountUsers: null,
        usersList: [],
        dataIsReturned: false,
        loader: true,
        usersCount: []
    });

    const columns = [
        {
            name: "id",
            selector: "id"
        },
        {
            name: "name",
            selector: "name"
        },
        {
            name: "email",
            selector: "email"
        },
        {
            name: "site_id",
            selector: "site_id"
        },
        {
            name: "created_at",
            selector: "created_at"
        }
    ];
    useEffect(() => {
        authenticationService.getUsersList().then(async (res) => {
            setState({
                dataIsReturn: true,
                amountUsers: res.length,
                usersList: res,
                loader: false
            });
            setData(res)
        })
    }, [])

    tableData = {
        columns,
        data,
    };


    return (
        <>
            {state.loader ? <div className="loaderDiv"><img src={loaderImage} className="img-fluid" alt="" /></div> : null}
            {(state.dataIsReturn &&
                <div className="usersListWrapper">
                    {/* <DataTableExtensions
                        print={false}
                        export={true}
                        filter={false}
                        {...tableData}
                    > */}
                        <table className='custom-table  table-responsive-md '>
                            <thead >
                                <tr className="headTr">
                                    <th>
                                        <h4 className="headerTrTh">
                                            #
                                        </h4>
                                    </th>
                                    <th>
                                        <h4 className="headerTrTh">
                                            Name
                                        </h4>
                                    </th>
                                    <th>
                                        <h4 className="headerTrTh">
                                            Email
                                        </h4>
                                    </th>
                                    <th>
                                        <h4 className="headerTrTh">
                                            Site
                                        </h4>
                                    </th>
                                    <th className='createdAt '>
                                        <h4 className="headerTrTh">
                                            Created at
                                        </h4>
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                {state.usersList.map((i, v) => {
                                    return <tr key={i.id} className='tbodyTr'>
                                        <td className="firsTd">
                                            <h4>{v + 1}</h4>
                                        </td>
                                        <td>
                                            <h4>{i.name}</h4>
                                        </td>
                                        <td>
                                            <h4>{i.email}</h4>
                                        </td>
                                        <td>
                                            <h4>{i.site_id === "*" ? "All sites" : i.site_id}</h4>
                                        </td>
                                        <td className='lastTd'>
                                            <h4>
                                                <Moment format="YYYY-MM-DD HH:mm:ss">{i.created_at}</Moment>
                                            </h4>
                                        </td>
                                    </tr>
                                }
                                )}
                            </tbody>
                        </table>
                    {/* </DataTableExtensions> */}
                </div>
            )}

        </>
    )
}



export default UsersList;