import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { default as Select } from "react-select";
import "./revoke-user-request.css";
import { authenticationService } from "../../services/API";
import loaderImage from "./../../Assets/images/loader.svg"
import { Redirect } from "react-router";

const RevokeUserRequest = () => {
    let [state, setState] = useState({
        user_id: null,
        comment: ""
    })
    let [loader, setLoader] = useState(false)
    const [succesNoty, setSuccesNoty] = useState(false)
    const [commentFiledReq, setCommentFiledReq] = useState("")
    const [selectReq, setSelectReq] = useState(false)
    const options = [];

    let [usersList, setUsersList] = useState(null)
    useEffect(() => {
        authenticationService.getUsersList().then(async (res) => {
            setUsersList(res)
        })
    }, [])

    if (usersList) {
        usersList.map((i, v) => {
            if (JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module == 2) {
                if (i.users_module !== 2) {
                    options.push({
                        value: i.id,
                        label: i.name + " | " + i.email
                    })
                }
            }
            else if (JSON.parse(localStorage.getItem('cfuCurrentUser')).user.users_module == 3) {
                if (i.users_module == 0) {
                    options.push({
                        value: i.id,
                        label: i.name + " | " + i.email
                    })
                }
            }
        })
    }
    // [
    //     { value: "1", label: "1" },
    //     { value: "2", label: "2" },
    //     { value: "3", label: "3" },
    // ];
    return (
        <>
            {succesNoty && (<Redirect
                to={{
                    pathname: "/revoke-user",
                    state: { succesNotify: true }
                }}
            />
            )
            }
            {loader ? <div className="loaderDiv"><img src={loaderImage} className="img-fluid" alt="" /></div> : null}
            <div className="backBtnHrDiv">
                <Link to="/revoke-user" className="toRevokeListLink">
                    Back
                </Link>
                <hr className="revokeUserHr"></hr>
            </div>
            <form onSubmit={(e) => {
                e.preventDefault();
                if (!state.user_id) {
                    setSelectReq(true)
                }
                if (state.user_id && state.comment) {
                    setLoader(true)
                    authenticationService.revokeUser(state.user_id, state.comment).then(res => {
                        localStorage.setItem("revokesLength", true)
                        setSuccesNoty(true)
                        setLoader(false)
                    }).catch(e => {
                        setCommentFiledReq(e.comment)
                        setLoader(false)
                    })
                }
            }
            }>
                <div className="revokeUserWrapper">
                    <div className="revokeUserSelectDiv">
                        <label>User Name</label>
                        <Select
                            options={options}
                            className={selectReq ? "selectFieldReq selectRevokeUser" : "selectRevokeUser"}
                            onChange={(e) => {
                                setSelectReq(false)
                                setState({
                                    ...state,
                                    user_id: e.value
                                })
                            }}
                        />
                        {selectReq && <div className="selectDivReq">Please Select User Name</div>}
                    </div>
                    <label>Comment</label>
                    <textarea required pattern="\S(.*\S)?" className={commentFiledReq ? "fieldReq" : null} id="comment" name="comment" value={state.comment} rows="4" cols="50" onChange={(e) => {
                        setCommentFiledReq("")
                        setState({
                            ...state,
                            comment: e.target.value
                        })
                    }}>
                    </textarea>
                    {commentFiledReq && <div className="commentFieldReq">{commentFiledReq}</div>}
                    <div className="revokeRequestFooter">
                        <button className='revokeUserBtn' type="submit" >Submit</button>
                    </div>
                </div>
            </form>
        </>)
}

export default RevokeUserRequest;