import React, { useEffect, useState } from "react";
import "./search.css";
import InputMask from "react-text-mask";
import { authenticationService } from "../../services/API";
import { Formik, Form } from "formik";
import ScoringResult from "../ScoringResult/ScoringResult";
import loaderImage from './../../Assets/images/loader.svg';
import 'react-toastify/dist/ReactToastify.css';

const getPatientStatus = (site_id, patient_id) => {
  return authenticationService.patientStatus(site_id, patient_id);
};

const Search = (props) => {
  const currentUser = JSON.parse(localStorage.getItem('cfuCurrentUser'));
  const [siteId, setSiteId] = useState(currentUser ? currentUser.user.site_id : null);
  const [patientStatusId, setPatientStatusId] = useState(null);
  const [patientId, setPatientId] = useState("");
  const [patientStatus, setPatientStatus] = useState("");
  const [loader, setLoader] = useState(false);
  const [idError, setIdError] = useState(false);
  const [noPatient, setNoPatient] = useState(false);
  const [scoring, setScoring] = useState(false);
  const validSiteId = new RegExp(/^\d{3}[-]\d{3}$/g);
  const validPatientId = new RegExp(/^\d{3}$/g);
  // const notify = () => toast.success("Wow so easy!", {
  //   position: "top-right",
  //   autoClose: 5000,
  //   hideProgressBar: false,
  //   closeOnClick: true,
  //   pauseOnHover: true,
  //   draggable: true,
  //   progress: undefined,
  // });
  useEffect(() => {
    const patientIdInput = document.getElementById('patientIdInput');
    if (siteId && siteId.length === 7 && siteId[siteId.length - 1] !== '_') {
      if (patientIdInput) {
        patientIdInput.focus()
      }
    }
  }, [siteId])

  useEffect(() => {
    const siteIdInput = document.getElementById('siteIdInput')
    if (siteId && siteId.length === 7 && siteId[siteId.length - 1] !== '_' && patientId.length === 0) {
      siteIdInput.focus()
    }
  }, [patientId])




  function submitPatientID() {
    if (!validSiteId.test(siteId)) {
      setIdError(true);
    }
    else if (!validPatientId.test(patientId)) {
      setIdError(true);
    }
    else { getResult(siteId, patientId); setLoader(true) };
  }
  function getResult(site_id, patient_id) {
    return getPatientStatus(site_id, patient_id)
      .then((res) => {
        if (res.status === 'error') {
          setNoPatient(true);
          setLoader(false);
        }
        else {
          setPatientStatus(res.status.text);
          setPatientStatusId(res.status.id)
          setLoader(false);
          setScoring(true);
          setLoader(false);
        }
      })
      .catch(function () {
        setLoader(false);
      });
  }
  return (
    <>
      {scoring ? (
        <ScoringResult patientId={patientId} siteId={siteId} setPatientId={setPatientId} patientStatusId={patientStatusId} patientStatus={patientStatus} scoring={scoring} setScoring={setScoring} />
      ) : (
        <div>
          {loader ? <div className="loaderDiv"><img src={loaderImage} className="img-fluid" alt="" /></div> : null}
          <div className="searchWrapper">
            <div className="searchHeader">
              <h3> Search </h3>
            </div>
            {/* <button onClick={notify}>Notiasdasdasdasdfy!</button>
            <ToastContainer className="" /> */}
            <div className="searchBtn">
              <Formik initialValues={{ id: "" }}>
                <Form>
                  <div className='maskInputsDiv'>
                    <div className='siteIdDiv'>
                      <label> Patient ID </label>
                      <InputMask
                        onChange={(e) => {
                          setSiteId(e.target.value);
                          setIdError(false);
                          setNoPatient(false)
                        }}
                        id='siteIdInput'
                        disabled={currentUser ? (currentUser.user.site_id !== '*' ? true : false) : null}
                        autoFocus={currentUser ? (currentUser.user.site_id === '*' ? "autofocus" : null) : null}
                        placeholder="xxx-xxx"
                        className={
                          idError || noPatient
                            ? "idErrorInput form-control searchInput"
                            : "form-control searchInput"
                        }
                        mask={[
                          /\d/,
                          /\d/,
                          /\d/,
                          "-",
                          /\d/,
                          /\d/,
                          /\d/,
                        ]}

                        defaultValue={isNaN(parseInt(currentUser ? currentUser.user.site_id.replace("-", "") : null)) ? null : parseInt(currentUser.user.site_id.replace("-", ""))}
                      />
                    </div>
                    <div className='patientSearchIdDiv'>
                      {/*<label></label>*/}
                      <InputMask
                        id='patientIdInput'
                        onChange={(e) => {
                          setPatientId(e.target.value);
                          setIdError(false);
                          setNoPatient(false)
                        }}
                        autoFocus={currentUser ? (currentUser.user.site_id !== '*' ? "autofocus" : null) : null}
                        placeholder="xxx"
                        className={
                          idError || noPatient
                            ? "idErrorInput form-control searchInput"
                            : "form-control searchInput"
                        }
                        mask={[
                          /\d/,
                          /\d/,
                          /\d/,
                        ]}
                      />
                    </div>
                  </div>
                  {noPatient ? (
                    <p className="noPatient">Patient ID does not exist</p>
                  ) : null}
                  {idError ? (
                    <p className="invalidIdMessage">Invalid Patient ID format</p>
                  ) : null}
                  <button onClick={submitPatientID}>Search</button>
                </Form>
              </Formik>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Search;